@use "../../scss/abstracts";

.wp-block-md-ageofunion-sample-dynamic {
  color: abstracts.$primary-theme-color;
}
.md_articles_listing {
  padding-top: 6.25rem;
  box-sizing: border-box;
  &__list {
    grid-column-gap: 1.25rem;
    grid-row-gap: 6.25rem;
    grid-template-columns: repeat(16, 1fr);
    padding: 10rem 0;
    border-top: 0;
    padding-top: 0;
    position: relative;
    border-bottom: 1.125rem solid #000;
    .md_articles_listing__item {
      &--article {
        position: relative;
        figure {
          overflow: hidden;
          border-radius: 2px;
          position: relative;
          margin: 0;
          padding: 0;
          img {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            width: 100%;
            -o-object-fit: cover;
            object-fit: cover;
            height: 100% !important;
            transition: opacity 0.6s 0.1s cubic-bezier(0.38, 0.005, 0.215, 1);
            opacity: 1;
          }
          &::before {
            display: block;
            padding-bottom: 111.76471%;
            width: 100%;
            content: "";
          }
        }
        .md_articles_listing__item--content {
          display: flex;
          padding-top: 0.625rem;
          &__info {
            width: 30%;
            padding-right: 1.25rem;
            text-transform: uppercase;
            font-weight: 500;
            font-size: 0.75rem;
            .md_articles_listing__item__tags {
              a {
                color: inherit;
                text-decoration: none;
                line-height: 1;
              }
            }
          }
          &__heading {
            .md_articles_listing__item__title {
              font-size: 1.375rem;
              font-weight: 400;
              width: 70%;
              line-height: 0.9;
              margin-bottom: 1.875rem;
              position: relative;
              margin: 0;
            }
            .md_articles_listing__item__excerpt {
              font-size: 1rem;
              line-height: 1.5;
              margin-bottom: 1.875rem;
            }
          }
        }
      }
    }
    .md_articles_listing__border_box {
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      min-height: 100%;
      border-top: 1.125rem solid #000;
      padding: 40px 0;
      &::before {
        height: 10.125rem;
        top: -2px;
        position: absolute;
        left: 50%;
        width: 1.25rem;
        margin-left: calc(-1 * 1.125rem / 2);
        content: "";
        background-color: #000;
        text-align: center;
      }
      h3 {
        font-size: 6.94444vw;
        letter-spacing: -0.045em;
        line-height: 0.8;
        text-transform: uppercase;
        padding-bottom: 5rem;
        font-weight: 500;
        position: relative;
      }
    }
    &::after {
      height: 10.125rem;
      bottom: -2px;
      position: absolute;
      left: 50%;
      width: 1.25rem;
      margin-left: calc(-1 * 1.125rem / 2);
      content: "";
      background-color: #000;
    }
  }
}

